<template>
  <div>
    
    
    
     <van-cell-group>
      <van-field
        required
        clearable
        label="输入小区名"
        placeholder="请输入小区名"
        @change="Change('name', $event)"
      />
      <van-row>
        <van-col span="12">
           <van-field
            type="digit"
            label="输入价格"
             placeholder="下限/元"
            
            required
            @change="Change('min_price', $event)"
          />
        </van-col>
        <van-col span="12" class='right-input'>
          <van-field
            required
             placeholder="上限/元"
            type="digit"
            @change="Change('max_price', $event)"
          />
        </van-col>
      </van-row>
      <van-field
        label="输入年代"
        placeholder="请输入年代 "
        @change="Change('time', $event)"
        required
      />
      <van-field
        label="输入地址"
        placeholder="请输入地址 "
        @change="Change('address', $event)"
        required
      />
    <van-button type="primary" size="large" @click="onAdd">保存</van-button>
    </van-cell-group>
    <ul class="container log-list">
      <li v-for="(log, index) in logs" :class="{ red: aa }" :key="index" class="log-item">
        <card :text="(index + 1) + ' . ' + log"></card>
      </li>
    </ul>
  </div>
</template>

<script>
import { formatTime } from '@/utils/index'
import card from '@/components/card'

export default {
  components: {
    card
  },

  data () {
    return {
      logs: []
    }
  },

  created () {
    const logs = (wx.getStorageSync('logs') || [])
    this.logs = logs.map(log => formatTime(new Date(log)))
  }
}
</script>

<style>
.log-list {
  display: flex;
  flex-direction: column;
  padding: 40rpx;
}

.log-item {
  margin: 10rpx;
}
</style>



